import { BrowserModule } from '@angular/platform-browser';
import { ErrorHandler, NgModule } from '@angular/core';
import { IonicApp, IonicErrorHandler, IonicModule } from 'ionic-angular';
import { credentials } from './config';
import { MyApp } from './app.component';
import { HomeComponent } from './components/home.component';
import { AddBillerComponent } from './components/add-biller.component';
import { BillerCardComponent } from './components/biller-card.component';
import { EditBillerComponent } from './components/edit-biller.component';
import { ReminderComponent } from './components/reminder.component';
import { NotificationComponent } from './components/notification.component';
import { AngularFireModule } from 'angularfire2';
import { AngularFirestoreModule } from 'angularfire2/firestore';
import { BillerService } from './services/biller.service';
import { OverviewComponent } from './components/overview.component';

@NgModule({
	declarations: [
		MyApp,
		HomeComponent,
		AddBillerComponent,
		BillerCardComponent,
		EditBillerComponent,
		ReminderComponent,
		NotificationComponent,
		OverviewComponent
	],
	imports: [
		BrowserModule,
		AngularFireModule.initializeApp(credentials.firebase, 'angularfs'),
		AngularFirestoreModule,
		IonicModule.forRoot(MyApp)
	],
	bootstrap: [IonicApp],
	entryComponents: [
		MyApp,
		HomeComponent,
		AddBillerComponent,
		EditBillerComponent,
		OverviewComponent
	],
	providers: [
		{ provide: ErrorHandler, useClass: IonicErrorHandler },
		BillerService
	]
})
export class AppModule { }



