import { Injectable } from '@angular/core';
import { AngularFirestore, AngularFirestoreCollection } from 'angularfire2/firestore'
import { Observable } from 'rxjs/Observable'
import { Biller } from '../models/biller'

@Injectable()
export class BillerService {

  billers: Observable<Biller[]>
  billerCollection: AngularFirestoreCollection<Biller>
  selectedBiller: Biller = {}
  currentBillers: Biller[] = []

  constructor(public afs: AngularFirestore) {
    this.billerCollection = this.afs.collection('billers', ref => ref.orderBy('dayOfMonth', 'asc'))
    this.billers = this.billerCollection.snapshotChanges().map(changes => {
      return changes.map(a => {
        const data = a.payload.doc.data() as Biller
        data.id = a.payload.doc.id
        return data
      })
    })
  }

  getBillers() {
    return this.billers
  }

  addBiller(biller: Biller) {
    this.billerCollection.add(biller)
  }

  updateBiller(updatedBiller: Biller): void {
    this.billerCollection.doc(updatedBiller.id).update(updatedBiller)
  }

  deleteBiller(biller: Biller) {
    this.billerCollection.doc(biller.id).delete();
  }

}


