import { Component, OnInit } from '@angular/core';
import { ModalController } from 'ionic-angular';
import { BillerService } from '../services/biller.service'
import { Biller } from '../models/Biller'
import { AddBillerComponent } from './add-biller.component';
import { EditBillerComponent } from './edit-biller.component';
import { OverviewComponent } from './overview.component';

@Component({
	selector: 'app-home',
	template: `
	
	<ion-header>
	<ion-navbar>
		<ion-title>Billers Overview</ion-title>
	</ion-navbar>
	</ion-header>

	<ion-content padding oncontextmenu="return false;">

		<!-- Alert for upcoming bills -->

		<!-- <app-reminder></app-reminder> -->

		<div *ngIf="billers?.length > 0;else noItems">


			<!-- Cards list unpaid -->
			<div *ngFor="let biller of billers">
				<div *ngIf="!biller.isPaid">
					<app-biller-card [inputBiller]="biller" (contextmenu)="presentEditModal(biller)">
					</app-biller-card>
				</div>
			</div>

			<hr>

			<!-- Cards list paid -->

			<div *ngFor="let biller of billers">
				<div *ngIf="biller.isPaid">
					<app-biller-card [inputBiller]="biller" (contextmenu)="presentEditModal(biller)">
					</app-biller-card>
				</div>
			</div>

		</div>


		<!-- Fab -->
		<ion-fab right bottom #fab>
			<button ion-fab color="primary"><ion-icon name="arrow-dropup"></ion-icon></button>
			<ion-fab-list side="top" (click)="fab.close()">
				<button ion-fab (click)="presentAddModal()"><ion-icon name="add"></ion-icon></button>
				<button ion-fab (click)="presentMoneyOverviewModal()"><ion-icon name="trending-up"></ion-icon></button>
			</ion-fab-list>
		</ion-fab>

	</ion-content>

	<ng-template #noItems>
		<hr>
		<h5>There are no billers in this list.</h5>
	</ng-template>
	
	`,
	styles: [`
		hr {
			height: 4px;
		}
	`]
})
export class HomeComponent implements OnInit {

	constructor(public billerService: BillerService, public modalCtrl: ModalController) { }

	ngOnInit() {
		this.billerService.getBillers().subscribe(billers => {
			this.billers = billers
		})
	}

	billers: Biller[]

	presentAddModal() {
		let modal = this.modalCtrl.create(AddBillerComponent);
		modal.present();
	}

	presentEditModal(selectedBiller: Biller) {
		this.billerService.selectedBiller = selectedBiller
		let modal = this.modalCtrl.create(EditBillerComponent);
		modal.present();
	}

	presentMoneyOverviewModal() {
		this.billerService.currentBillers = this.billers
		let modal = this.modalCtrl.create(OverviewComponent);
		modal.present();
	}



}


