import { Component, OnInit } from '@angular/core';
import { ViewController, ToastController, AlertController } from 'ionic-angular';
import { BillerService } from '../services/biller.service'
import { Biller } from '../models/Biller'

@Component({
	selector: 'app-overview',
	template: `
		<ion-header>
	<ion-navbar>
		<ion-title>Overview</ion-title>
		<ion-buttons end>
			<button ion-button color="primary" (click)="closeModal()">Close</button>
		</ion-buttons>
	</ion-navbar>
	</ion-header>

	<ion-content padding oncontextmenu="return false;">

	<ion-card>

		<ion-item (contextmenu)="presentEditModalIncome()">
			<ion-icon name="add" item-left large style="color:green"></ion-icon>
			<h2>Monthly Income:</h2>
			<p>$ {{monthlyIncome}}</p>
		</ion-item>

		<ion-item>
			<ion-icon name="remove" item-left large style="color:red"></ion-icon>
			<h2>Monthly amount for bills:</h2>
			<p>$ {{getMonthlyAmountBills}}</p>
		</ion-item>

		<hr>

		<ion-item>
			<ion-icon name="arrow-forward" item-left large color="primary"></ion-icon>
			<h2>Monthly money left:</h2>
			<p>$ {{ getMonthlyMoneyLeft.toFixed(2) }}</p>
		</ion-item>

	</ion-card>

	<app-notification></app-notification>

	</ion-content>
 
 `
})
export class OverviewComponent implements OnInit {

	constructor(public viewCtrl: ViewController, public billerService: BillerService, public toastCtrl: ToastController, public alertCtrl: AlertController) { }

	ngOnInit() {
		this.billers = this.billerService.currentBillers
	}

	billers: Biller[]
	monthlyIncome: number = 2630

	get getMonthlyAmountBills(): number {
		let amountAllBills: number = 0

		for (let biller of this.billers) {
			amountAllBills += biller.amount
		}

		return amountAllBills
	}

	get getMonthlyMoneyLeft(): number {
		let moneyLeft: number = 0

		moneyLeft = this.monthlyIncome - this.getMonthlyAmountBills

		return moneyLeft
	}

	presentEditModalIncome() {

		let alert = this.alertCtrl.create({
			title: `Monthly Income: ${this.monthlyIncome}`,
			inputs: [
				{
					name: 'monthlyIncome',
					placeholder: 'monthly income',
					value: `${this.monthlyIncome}`,
					type: 'number'
				}
			],
			buttons: [
				{
					text: 'Cancel',
					role: 'cancel',
					handler: data => {
						console.log('Cancel clicked');
					}
				},
				{
					text: 'Save',
					handler: data => {
						this.monthlyIncome = data.monthlyIncome
					}
				}
			]
		});
		alert.present();
	}


	closeModal() {
		this.viewCtrl.dismiss();
	}



}


