import { Component, OnInit, Input } from '@angular/core';
import { Biller } from '../models/Biller'

@Component({
	selector: 'app-biller-card',
	template:
		`
		<ion-card [ngClass]="setCardClasses()">
			<div class="card-biller-due">
			{{inputBiller.dayOfMonth}}
			</div>
			<div class="card-biller-image">
			<img [src]="inputBiller.imageUrl" />
			</div>
			<div class="card-biller-title">
			{{inputBiller.title}}
			</div>
			<div class="card-biller-price" [ngClass]="setPriceClasses()">
			$ {{ inputBiller.amount }}
			</div>
		</ion-card>
	`,
	styles: [`
  
	.isPaid{
		color: green;
		text-decoration: line-through;
	  }
	  
	  .isNotPaid{
		color: red;
		font-weight: bold;
	  }
	  
	  .isPaidStripe{
		border-right: 3px solid green;
	  }
	  
	  .isNotPaidStripe{
		border-right: 3px solid red;
	  }
	  
	  ion-card{
		  display: grid;
		  grid-template-columns: 1fr 2fr 2fr 1fr;
		  grid-template-areas: 'card-biller-due card-biller-image card-biller-title card-biller-price';
		  padding-right: 10px;
		  min-width: 250px;
		  min-height: 120px;
	  }
	  
		.card-biller-due{
		  color: rgba(0,0,0,.54);
		  font-size: 16px;
		  grid-area: card-biller-due;
		  justify-self: center;
		  align-self: center;
		}
	  
		.card-biller-image{
		  width: 100px;
		  grid-area: card-biller-image;
		  justify-self: start;
		  align-self: center;
		}
	  
		.card-biller-title{
		  font-size: 16px;
		  grid-area: card-biller-title;
		  justify-self: start;
		  align-self: center;
		}
	  
		.card-biller-price{
		  font-size: 16px;
		  grid-area: card-biller-price;
		  justify-self:end;
		  align-self: center;
		}
	
	`]
})
export class BillerCardComponent implements OnInit {

	constructor() { }

	ngOnInit() {

	}

	@Input() public inputBiller: Biller = {}

	setPriceClasses() {
		let classes = {
			isPaid: this.inputBiller.isPaid,
			isNotPaid: !this.inputBiller.isPaid
		};
		return classes;
	}

	setCardClasses() {
		let classes = {
			isPaidStripe: this.inputBiller.isPaid,
			isNotPaidStripe: !this.inputBiller.isPaid
		};
		return classes;
	}

}


