import { Component, OnInit } from '@angular/core';
import { ViewController } from 'ionic-angular';
import { Biller } from '../models/Biller'
import { BillerService } from '../services/biller.service'
import GoogleImageSearch from '../classes/GoogleImageSearch.js'

@Component({
	selector: 'app-add-biller',
	template: `
		<ion-header>
	<ion-navbar>
		<ion-title>Add Biller Modal</ion-title>
		<ion-buttons end>
			<button ion-button color="primary" (click)="closeModal()">Close</button>
		</ion-buttons>
	</ion-navbar>
</ion-header>

<ion-content padding>

	<ion-list>

		<ion-item>
			<ion-label fixed>Title</ion-label>
			<ion-input type="text" [(ngModel)]="newBiller.title"></ion-input>
		</ion-item>

		<ion-item>
			<ion-label fixed>Amount</ion-label>
			<ion-input type="number" [(ngModel)]="newBiller.amount"></ion-input>
		</ion-item>

		<ion-item>
			<ion-label fixed>Day of Month</ion-label>
			<ion-input type="number" [(ngModel)]="newBiller.dayOfMonth"></ion-input>
		</ion-item>

		<ion-item>
			<ion-label fixed>Image Url</ion-label>
			<ion-input type="text" [(ngModel)]="newBiller.imageUrl"></ion-input>
		</ion-item>

		<ion-searchbar placeholder='search google for image' [(ngModel)]="searchTerm" debounce=500 (ionInput)="onInput($event)">
		</ion-searchbar>

	</ion-list>

	<button ion-button block (click)="addNewBiller(newBiller)">Add New Biller</button>

	<ion-card-content>
		Preview:
	</ion-card-content>

	<app-biller-card [inputBiller]="newBiller"></app-biller-card>

</ion-content>
	
	`
})
export class AddBillerComponent implements OnInit {

	constructor(public viewCtrl: ViewController, public billerService: BillerService) { }

	ngOnInit() {
	}

	newBiller: Biller = {
		title: 'Jackpot',
		imageUrl: 'https://dummyimage.com/mediumrectangle/222222/eeeeee',
		amount: 99,
		dayOfMonth: 1,
		isPaid: false
	}

	searchTerm: string
	foundImages: Array<any>

	closeModal() {
		this.viewCtrl.dismiss();
	}

	onInput($event) {
		if (this.searchTerm.length > 3) {
			this.makeAjaxRequest(this.searchTerm)
		}
	}

	makeAjaxRequest(searchTerm: string): void {

		// console.log('makeing ajax request')
		GoogleImageSearch.searchImage(searchTerm)
			.then((res: Array<any>) => {
				this.foundImages = res
				// this.searchIcon = "search"
				this.newBiller.imageUrl = this.foundImages[0]
			})
	}

	addNewBiller(newBiller: Biller) {
		if (newBiller.title.length >= 3) {
			newBiller.amount *= 1
			newBiller.dayOfMonth *= 1
			this.billerService.addBiller(newBiller)
			this.closeModal()
		} else {
			alert("Please specify a title with at least 3 letters.")
		}

	}

}


