import { Component, OnInit } from '@angular/core';
import { ViewController, ToastController, AlertController } from 'ionic-angular';
import { BillerService } from '../services/biller.service'
import { Biller } from '../models/Biller'

@Component({
	selector: 'app-edit-biller',
	template: `
  
	<ion-header>
		<ion-navbar>
			<ion-title>Edit Biller Modal</ion-title>
			<ion-buttons end>
				<button ion-button color="primary" (click)="closeModal()">Close</button>
			</ion-buttons>
		</ion-navbar>
	</ion-header>

	<ion-content padding>

		<ion-list>

			<ion-item>
				<ion-label fixed>Title</ion-label>
				<ion-input type="text" [(ngModel)]="selectedBiller.title"></ion-input>
			</ion-item>

			<ion-item>

				<ion-label fixed>Amount</ion-label>
				<ion-input type="number" [(ngModel)]="selectedBiller.amount"></ion-input>
			</ion-item>

			<ion-item>
				<ion-label fixed>Day of Month</ion-label>
				<ion-input type="number" [(ngModel)]="selectedBiller.dayOfMonth"></ion-input>
			</ion-item>

			<ion-item>
				<ion-label fixed>Image Url</ion-label>
				<ion-input type="text" [(ngModel)]="selectedBiller.imageUrl"></ion-input>
			</ion-item>

			<ion-item>
				<ion-label fixed>Paid Status</ion-label>
				<ion-toggle [(ngModel)]="selectedBiller.isPaid" color="secondary"></ion-toggle>
			</ion-item>

		</ion-list>

		<button ion-button block (click)="updateSelectedBiller(selectedBiller)">Update Selected Biller</button>

		<ion-card-content>
			Preview:
		</ion-card-content>

		<app-biller-card [inputBiller]="selectedBiller"></app-biller-card>

		<button ion-button color="danger" block (click)="deleteSelectedBiller(selectedBiller)" float-end>Delete Selected Biller</button>



	</ion-content>
 
 
 `,
})
export class EditBillerComponent implements OnInit {

	constructor(public viewCtrl: ViewController, public billerService: BillerService, public toastCtrl: ToastController, public alertCtrl: AlertController) { }

	ngOnInit() {
		Object.assign(this.selectedBiller, this.billerService.selectedBiller)
	}

	selectedBiller: Biller = {}

	check() {
		console.log(this.selectedBiller)
	}

	closeModal() {
		this.viewCtrl.dismiss();
	}

	updateSelectedBiller(selectedBiller: Biller) {
		if (selectedBiller.title.length >= 3) {
			selectedBiller.amount *= 1
			selectedBiller.dayOfMonth *= 1
			this.billerService.updateBiller(selectedBiller)
			this.closeModal()
		} else {
			alert('Title must be at least 3 letters')
		}
	}

	deleteSelectedBiller(selectedBiller: Biller) {

		this.showConfirm(`Are you sure that you want to delete: ${selectedBiller.title}?`).then((result) => {
			if (result) {
				this.billerService.deleteBiller(selectedBiller)
				this.closeModal()
				let toast = this.toastCtrl.create({
					message: `Biller:${selectedBiller.title} was deleted successfully.`,
					duration: 3000
				});
				toast.present();
			}
		})
	}

	showConfirm(message: string): Promise<boolean> {

		return new Promise((resolve, reject) => {

			this.alertCtrl.create({
				title: 'Please confirm',
				message: message,
				buttons: [
					{
						text: 'No',
						handler: _ => resolve(false)
					},
					{
						text: 'Yes',
						handler: _ => resolve(true)
					}
				]
			}).present();
		})
	}

}


