import { Component, OnInit, Input } from '@angular/core';

@Component({
	selector: 'app-reminder',
	template: `
		<ion-card>
			<ion-icon name='information-circle'></ion-icon>
			<div class="description">
			This is a reminder...test
			</div>
		</ion-card>
 	`,
	styles: [`
		ion-card{
			min-height: 40px;
			display: grid;
			grid-template-columns: 40px 1fr;
			grid-template-areas: 'symbol description description';
			background: rgba(239, 10, 10, 0.72); 
			color: white;
		}
		
		ion-icon{
			grid-area: symbol;
			/* padding-left: 16px; */
			justify-self:center;
			align-self: center;
		}
		
		.description{
			grid-area: description;
			justify-self:start;
			align-self: center;
		}
		
		.due-today{
			background-color: red;
		}
	`]
})
export class ReminderComponent implements OnInit {

	constructor() { }

	ngOnInit() {
	}

	@Input() level: string = 'information-circle'

	//   setIconClasses() {
	//     let classes = {
	//       isPaidStripe: this.inputBiller.isPaid,
	//       isNotPaidStripe: !this.inputBiller.isPaid
	//       //     background: rgba(68, 210, 255, 0.73); light blue
	//     };
	//     return classes;
	//   }

}


